.boton-contenedor{
    height: 83px;
    display: flex;
    flex: 1 1;  /*elementos que se distribuye de forma equitativa*/
    justify-content: center;
    align-items: center;
    font-weight: bold;
    font-size: 38px;
    background-color: #1b1b32;
    color: white;
    border: 2px solid white;
    border-radius: 25px;
    margin: 4px;
    cursor: pointer;
    user-select: none; /*no se puede seleccionar el texto  */

}

.boton-contenedor:hover{
    background-color: #5a01a7;
}

.operador{
    background-color: #00471b;
}
.operador:hover{
    background-color: #ff6600;
}